/* eslint-disable @typescript-eslint/no-unused-vars */
import low from "lowdb";
import LocalStorage from "lowdb/adapters/LocalStorage";
import { cloneDeep } from "lodash";

const adapter = new LocalStorage("fast-admin");
const db = low(adapter);

db.defaults({
  database: {}
}).write();

export default db;

/**
 * @description  
 * @param {Object} payload dbName {String} 
 * @param {Object} payload path {String} 
 * @param {Object} payload user {Boolean} 
 * @param {Object} payload validator {Function}   true 
 * @param {Object} payload defaultValue {*} 
 * @returns {String} 
 */
export function pathInit({
  dbName = "database",
  path = "",
  validator = (...args: unknown[]) => true,
  defaultValue = ""
}) {
  const currentPath = `${dbName}.${path}`;
  const value = db.get(currentPath).value();
  if (!(value !== undefined && validator(value))) {
    db.set(currentPath, defaultValue).write();
  }
  return currentPath;
}

/**
 * @description  | 
 * @description  dbName.path = value
 * @param {Object} payload dbName {String} 
 * @param {Object} payload path {String} 
 * @param {Object} payload value {*} 
 * @param {Object} payload user {Boolean} 
 */
export function dbSet({ dbName = "database", path = "", value = "" }) {
  db.set(
    pathInit({
      dbName,
      path
    }),
    value
  ).write();
}

/**
 * @description 
 * @description  dbName.path || defaultValue
 * @param {Object} payload dbName {String} 
 * @param {Object} payload path {String} 
 * @param {Object} payload defaultValue {*} 
 * @param {Object} payload user {Boolean} 
 */
export function dbGet({ dbName = "database", path = "", defaultValue = "" }) {
  return new Promise(resolve => {
    resolve(
      cloneDeep(
        db
          .get(
            pathInit({
              dbName,
              path,
              defaultValue
            })
          )
          .value()
      )
    );
  });
}

/**
 * @description 
 * @param {Object} payload user {Boolean} 
 */
export function database({
  dbName = "database",
  path = "",
  validator = () => true,
  defaultValue = ""
} = {}) {
  return new Promise(resolve => {
    resolve(
      db.get(
        pathInit({
          dbName,
          path,
          validator,
          defaultValue
        })
      )
    );
  });
}
