import { database as getDatabase, dbGet, dbSet } from "@/libs/db";

export default {
  namespaced: true,
  actions: {
    /**
     * @description  | 
     * @description  dbName.path = value
     * @param {Object} context
     * @param {Object} payload dbName {String} 
     * @param {Object} payload path {String} 
     * @param {Object} payload value {*} 
     */
    set(
      context: any,
      { dbName = "database", path = "", value = "", global = false }
    ) {
      if (!global) {
        path = `${context.rootState.projectName}.${path}`;
      }
      dbSet({ dbName, path, value });
    },
    /**
     * @description 
     * @description  dbName.path || defaultValue
     * @param {Object} context
     * @param {Object} payload dbName {String} 
     * @param {Object} payload path {String} 
     * @param {Object} payload defaultValue {*} 
     */
    get(
      context: any,
      { dbName = "database", path = "", defaultValue = "", global = false }
    ) {
      if (!global) {
        path = `${context.rootState.projectName}.${path}`;
      }
      return dbGet({ dbName, path, defaultValue });
    },
    /**
     * @description 
     * @param {Object} context
     */
    database() {
      return getDatabase({
        defaultValue: ""
      });
    }
  }
};
