



































import { post } from "@/libs/request";
import { TemplateConfig } from "@/types";
import useRouter from "@/use/useRouter";
import { defineComponent } from "@vue/composition-api";
import { Message } from "element-ui";
export const ViewRootDevPath =
  "C:\\all\\code\\cli\\fast-admin-view\\fast-admin-view-default\\src\\page";

export default defineComponent({
  props: {
    list: Array
  },
  setup() {
    const router = useRouter();
    const onDownload = async (item: TemplateConfig) => {
      const res = await post("/template/downloadTemplate", {
        name: item.name,
        output: ViewRootDevPath,
        url: item.url
      });

      if (res.code === 200) {
        Message.success("");
      } else {
        Message.success("");
      }
    };
    const toDetail = (item: TemplateConfig) => {
      router?.push(`/template/detail/${item.id}`);
    };
    return {
      onDownload,
      toDetail
    };
  }
});
