
























































import { defineComponent, ref, toRaw } from "@vue/composition-api";
import { camelCase, upperFirst } from "lodash";

export default defineComponent({
  name: "CreateRouteForm",
  props: {
    viewPathPrefix: {
      type: String,
      default: "@/views/"
    }
  },
  setup(props, ctx) {
    const formData = ref({
      path: "",
      name: "",
      icon: "el-icon-document",
      title: "",
      viewPath: "",
      hidden: false,
      children: []
    });
    const formRules = ref({
      path: [{ required: true, message: "", trigger: "blur" }],
      name: [{ required: true, message: "", trigger: "blur" }],
      viewPath: [{ required: true, message: "", trigger: "blur" }]
    });

    /**
     * pathnameviewPath
     */
    const onPathInput = (e: any) => {
      const val = e.target.value;
      let name = val;
      if (val.charAt(0) === "/") {
        name = val.substring(1);
      }
      formData.value.path = val;
      formData.value.name = upperFirst(camelCase(name));
      formData.value.viewPath = `${props.viewPathPrefix}${formData.value.name}.vue`;
    };

    const form = ref<null | any>(null);
    const onConfirm = () => {
      form.value?.validate((valid: boolean) => {
        if (valid) {
          ctx.emit("success", toRaw(formData.value));
        } else {
          ctx.emit("fail");
          return false;
        }
      });
    };

    const resetFields = () => {
      form.value?.resetFields();
    };

    const onCancel = () => {
      resetFields();
      ctx.emit("cancel");
    };

    return {
      form,
      formData,
      formRules,
      onPathInput,
      onConfirm,
      onCancel,
      resetFields
    };
  }
});
